import React from "react";
import { Button, Grid } from "@mui/material";
import { useNavigate } from "react-router-dom";

const MainPage = () => {
  const navigate = useNavigate();

  return (
    <Grid>
      <Button>
        <Button
          fullWidth
          variant="contained"
          onClick={() => {
            navigate("/square-equation");
          }}
        >
          Rwnanie kwadratowe
        </Button>
      </Button>
    </Grid>
  );
};

export default MainPage;
