import React from "react";
import { useState } from "react";
import { Grid, TextField, TextareaAutosize, Button } from "@mui/material";
import SquareEquationService from "../../services/squareEquation/SquareEquationService";

export interface Equation {
  equation: string;
}

export interface EquationResult {
  solution: string;
  hint: string;
}

const SquareEquation = () => {
  const [eq, setEq] = useState<Equation>({
    equation: "",
  });

  const [res, setRes] = useState<EquationResult>({
    solution: "",
    hint: "",
  });

  const solveSquareEquation = () => {
    SquareEquationService.solveSquareEquation(eq)
      .then((response) => {
        console.log(response.data);
        console.log(response.data.solution);
        setRes(response.data);
      })
      .catch((error) => {
        console.log(error);
      });
  };

  return (
    <Grid
      container
      justifyContent="center"
      display="flex"
      alignItems="center"
      marginTop={8}
    >
      <Grid item width={1000}>
        <Grid item>
          <TextField
            fullWidth
            placeholder="Wpisz rwnanie kwadratowe..."
            value={eq.equation ? eq.equation : ""}
            onChange={(event) => setEq({ ...eq, equation: event.target.value })}
          ></TextField>
        </Grid>
        <Grid item marginTop={2}>
          <Button
            fullWidth
            variant="contained"
            onClick={() => {
              solveSquareEquation();
            }}
          >
            Rozwi
          </Button>
        </Grid>
      </Grid>
      <Grid item marginTop={10}>
        <TextareaAutosize
          placeholder="Wynik"
          value={res.solution ? res.solution : ""}
          style={{ width: 1000 }}
        />
      </Grid>
      <Grid item marginTop={10}>
        <TextareaAutosize
          placeholder="Wskazwka"
          value={res.hint ? res.hint : ""}
          style={{ width: 1000 }}
        />
      </Grid>
    </Grid>
  );
};

export default SquareEquation;
