import React from "react";
import { Grid, Typography } from "@mui/material";
import "./appBar.css";
import Logo from "../../assets/Logo.png";

const AppBar = () => {
  return (
    <Grid container component="nav" display="flex">
      <Grid item>
        <img src={Logo} width={70} />
      </Grid>
      <Grid item paddingLeft={92} paddingTop={2}>
        <Typography variant="h2" color="#4C87BA">
          CalcFree
        </Typography>
      </Grid>
    </Grid>
  );
};

export default AppBar;
